body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}
.container{
  margin:0 auto;
  
}
.titleItem{
  background-color:black;
  color:yellowgreen;
}
.anchor{
  display:block;
  background-color:rgb(203, 223, 93);
  color:rgb(9,10,95);

}
.genreChangetitle{
 
  color:rgb(9, 10, 95);
  display:block;
  flex-shrink:1;
  padding-right:20px;
}
.movieList{
  display:grid;
  
 grid-template-columns: repeat(12,1fr);
 
 
 grid-column-gap:10px;
 grid-row-gap:10px;
 align-content:space-between;
 
 
 margin:0 auto;

}

.movie-item{
  grid-column:span 3;
 
  position:relative;
  color:rgb(203, 223, 93);
  list-style-type:none;
  background-color:rgb(9, 10, 95);
  
}
.movie-poster{
  width:100%;
}
.movie-title{
  display:block;
  height:30px;
 font-weight:bold;
 font-size:1em;
}
.selection{
  background-color:rgb(203,223,93);
  width:150px;
  height:40px;
  color:rgb(9, 10, 95);
}
.page{
  margin:0 auto;
  display:flex;
  align-items:center;
  justify-content:center;
  
  
}
.page button{
  margin:20px;
}

button{
  background-color:rgb(203,223,93);
  border:2px dashed rgb(9, 10, 95);
  border-radius:10px;
  margin-left:50px;
}
.page p{
  display:block;
  background-color:rgb(203, 223, 93);
  color:rgb(9,10,95);
}
footer{
  display:flex;
  height:50px;
  flex-wrap:wrap;
  align-items:center;
  justify-content:space-around;
}
@media only screen and (max-width: 1000px){
  .movie-item{
    grid-column:span 5;
  }
    
}
@media only screen and (max-width:601px){
  .movie-item{
  grid-column:span 10;}
  
}
